#joinDiv{
    margin: 50px auto;
    width: 70%;
    height: 70vh;
    border-radius: 25px;
    border: 3px solid rgb(0, 165, 0);
    position: relative;
    box-shadow: 0px 0px 5px rgba(48,48,48,0.5);
}

#joinLogo{
    margin: 20px auto 50px;
    width: 50%;
    height: 14%;
    border-radius: 15px;
    background-color: rgb(0, 165, 0);
}

#joinDiv label{
    margin: 30px auto;
    width: 60%;
    height: 5%;
    display: block;
}

#joinDiv input[type=text], #joinDiv input[type=password]{
    width: 80%;
    height: 100%;
    border: none;
    border-radius: 10px;
    background-color: rgb(240, 240, 240);
    display: block;
    text-indent: 10px;
    float: right;
}

#joinDiv input[type=file]{
    margin: 30px auto;
    display: block;
}

#joinSubmit{
    margin: 20px auto;
    width: 55%;
    height: 7%;
    border: none;
    border-radius: 5px;
    color: rgb(255,255,255);
    font-size: 18px;
    font-weight: bold;
    background-color: rgb(48, 48, 48);
    transition: all 0.2s;
    display: block;
}

#joinSubmit:active{
    color: rgb(48, 48, 48);
    border: 1px solid rgb(48, 48, 48);
    background-color: rgb(255,255,255);
}