#createDiv{
    top: 0;
    position: fixed;
    width: 100%;
    height: 100%;
    overflow: auto;
    background-color: rgb(240, 240, 240);
}

/* 상단 메뉴 */
#createHeader{
    width: 100%;
    height: auto;
    z-index: 1;
}

/* 상단 메뉴 윗부분 */
#createMainMenu{
    width: 100%;
    min-height: 5vh;
    background-color:rgb(0, 165, 0);
    position: relative;
}



#imageUpload{
    display: none;
}



/* 제작페이지 몸통 */
#createBody{
    
    position: relative;
}

#createBody > div{
    top: 0;
    height: 90vh;
}

.ql-container{
    margin: 0 auto;
    width: 45%;
    min-width: 300px;
    top: 0;
    height: 100%;
    background-color: rgb(255,255,255);
}


.page {
    width: 21cm;
    min-height: 29.7cm;
    padding: 1.5cm 1.5cm 2cm 1.5cm;
}
  
@page {
    size: A4;
    margin: 0;
}
  
@media print {
    /* 출력할 때 원하는 부분을 제외하고 전부 가리는 것 */
    .ql-toolbar, #createHeader{ display:none }
    .ql-container {
        width: 100%;
        min-height: 100vh;
    }
    * {
        -webkit-print-color-adjust: exact;
        print-color-adjust: exact;
    }


    .page {
      margin: 0;
      border: initial;
      border-radius: initial;
      width: initial;
      min-height: initial;
      box-shadow: initial;
      background: initial;
      page-break-after: always;
    }
}





/* 공식 스타일 */
.controls {
    display: flex;
    border: 1px solid #ccc;
    border-top: 0;
    padding: 10px;
}
  
.controls-right {
    margin-left: auto;
}
  
.state {
    margin: 10px 0;
    font-family: monospace;
}
  
.state-title {
    color: #999;
    text-transform: uppercase;
}